import React from 'react';
import Profile from '../../Images/profile.svg';
import {
    Container,
    Wrapper,
    InfoArea,
    Text,
    TextSpan,
    Description,
    HireButton,
    ImageArea,
    ImageProfile,
} from './styles';

const Hero = () => {
    return (
        <Container id='hero'>
            <Wrapper>
                <InfoArea>
                    <Text>Ol, Eu sou um<br />
                        <TextSpan>Desenvolvedor Front-End</TextSpan>
                    </Text>
                    <Description>
                        Profissional formado na rea de Sistemas de Informao, com experincia
                        na rea de desenvolvimento de Front-End para aplicaes Web.
                    </Description>
                    <HireButton>Contrate-me</HireButton>
                </InfoArea>
                <ImageArea>
                    <ImageProfile src={Profile} />
                </ImageArea>
            </Wrapper>
        </Container>
    )
}

export default Hero;